<template>
  <div>
  <div class="blankTop">
    <blank-top></blank-top>
    <div class="banner-top">
    <span>
        <img src="http://www.bjcipt.com/zixun2.0/images/11111.png" alt="">
      </span>
    </div>
  </div>
  <div class="login_container">
    <!---->
    <div class="login_box">
      <!---->
      <div class="avatar_box">
        <img src="../../assets/logo.png">
      </div>
      <!---->
      <el-form ref="loginFormRef" :model="loginForm" :rules="loginFormRules" class="login_form">
        <el-form-item label=" :" prop="phone">
          <el-input v-model="loginForm.phone" prefix-icon="iconfont icon-user"></el-input>
        </el-form-item>
        <el-form-item label=" :" prop="password">
          <el-input type="password" v-model="loginForm.password" prefix-icon="iconfont icon-3702mima"></el-input>
        </el-form-item>
        <el-form-item class="buttons">
          <span class="register" @click="dialogVisible = true"></span>
          <el-button type="primary" @click="login"></el-button>
          <el-button type="info" @click="resetLoginForm"></el-button>
        </el-form-item>
      </el-form>
    </div>
    <!---->
    <el-dialog title="" :visible.sync="dialogVisible" width="30%" @close="dialogClosed">
      <el-form :model="info" :rules="infoRules" ref="infoRef" label-width="80px">
        <el-form-item label="" prop="phone">
          <el-input v-model="info.phone"></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input v-model="info.password"></el-input>
        </el-form-item>
        <el-form-item label="" prop="type">
          <el-radio-group v-model="info.type">
            <el-radio label="teacher"></el-radio>
            <el-radio label="student"></el-radio>
          </el-radio-group>
        </el-form-item>
      </el-form>
      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="addTeaOrStu" > </el-button>
      </span>
    </el-dialog>
  </div>
  </div>
</template>

<script>
import BlankTop from '../home/components/BlankTop'
export default {
  components: { BlankTop },
  inject: ['reload'],
  data () {
    const checkMobile = (rule, value, cb) => {
      // 
      const regMobile = /^(0|86|17951)?(13[0-9]|15[012356789]|17[678]|18[0-9]|14[57])[0-9]{8}$/
      if (regMobile.test(value)) {
        return cb()
      }
      cb(new Error(''))
    }
    return {
      isRouterAlive: true,
      info: {
        type: '',
        phone: '',
        password: ''
      },
      infoRules: {
        password: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            min: 6,
            max: 15,
            message: '6~15',
            trigger: 'blur'
          }
        ],
        phone: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            validator: checkMobile,
            trigger: 'blur'
          }
        ],
        type: [
          {
            required: true,
            message: '',
            trigger: 'change'
          }
        ]
      },
      dialogVisible: false,
      loginForm: {
        phone: '',
        password: ''
      },
      loginFormRules: {
        // 
        phone: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            validator: checkMobile,
            trigger: 'blur'
          }
        ],
        // 
        password: [
          { required: true, message: '', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    addTeaOrStu () {
      this.$refs.infoRef.validate(async valid => {
        if (!valid) return
        const { data: res } = await this.$http.post('http://localhost:8080/student/register', this.info)
        console.log(res)
        console.log(res.code)
        if (res.code !== 200) return this.$message.error('')
        this.$message.success('')
        this.reload()
      })
    },
    dialogClosed () {
      this.$refs.infoRef.resetFields()
    },
    // 
    resetLoginForm () {
      this.$refs.loginFormRef.resetFields()
    },
    login () {
      this.$refs.loginFormRef.validate(async valid => {
        if (!valid) return
        const { data: res } = await this.$http.post('http://localhost:8080/student/login', this.loginForm)
        console.log(res)
        console.log(res.code)
        window.sessionStorage.setItem('loginId', res.loginId)
        if (res.code !== 200) return this.$message.error('')
        this.$message.success('')
        window.sessionStorage.setItem('token', res.token)
        if (res.loginType === 'student') {
          const routeData = this.$router.resolve('/student')
          window.open(routeData.href, '_blank')
        }
        if (res.loginType === 'teacher') {
          const routeData = this.$router.resolve('/teacher')
          window.open(routeData.href, '_blank')
        }
      })
    }
  }
}
</script>

<style lang="less" scoped>
  .banner-top {
    height: 120px;
    text-align: center;
  }

  .banner-top img {
    width: 100%;
    height: 100%;
  }
  .register {
    cursor: pointer;
    margin-right: 20px;
    font-style: italic;
    text-decoration: underline;
  }
  .login_container{
    background-color: #ffffff;
    height: 100%;
  }
  .login_box{
    margin-top: 60px;
    width: 450px;
    height: 300px;
    background-color: rgba(218, 195, 238, 0.85);
    border-radius: 3px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    .avatar_box{
      height: 130px;
      width: 130px;
      border: 1px solid #eee;
      border-radius: 50%;
      padding: 10px;
      box-shadow: 0 0 10px #ddd;
      position: absolute;
      left: 50%;
      transform: translate(-50%,-50%);
      background-color: #fff;
      img{
        width: 100%;
        height: 100%;
        border-radius: 50%;
        background-color: rgba(238, 238, 238, 0.7);
      }
    }
  }
  .login_form{
    position: absolute;
    bottom: -20px;
    width: 100%;
    padding: 0px 30px;
    box-sizing: border-box;
  }
  .buttons{
    display: flex;
    justify-content: flex-end;
  }
</style>
